import Form, { useForm } from "@miq/form";
import { Icons, IconButton, IconNavLink } from "@miq/components";
import { useEffect, useState } from "react";
import { AdminView } from "@miq/admin";
import { docServices } from "../utils";
import DocuFileGrid from "../DocuFileGrid";

import "./document-update-view.scss";

import DocumentShareModal from "./DocumentShareModal/";
import DocumentUpdateForm from "./DocumentUpdateForm";

export const ViewHeader = (props) => {
    const { doc } = props;
    const [isOpen, setOpen] = useState(false);

    return (
        <div className="document-update-header my-2">
            <div className="pe-1">
                <IconNavLink
                    to={props.match.path.replace(":docSlug/", "")}
                    Icon={Icons.ArrowLeft}
                    title="Retourner en arriere"
                />
            </div>
            <div className="flex-1">
                <Form.TextInput
                    required
                    name="title"
                    onSave={props.handleTextInputSave}
                    disabled={doc.is_read_only}
                    autoComplete="off"
                    tabIndex={0}
                    aria-label="Renommer ce document"
                    title="Renommer ce document"
                    data-tooltip="Renommer ce document"
                />
            </div>

            <div className="ps-1">
                <IconButton
                    primary
                    onClick={() => setOpen(true)}
                    disabled={doc.is_read_only}
                    Icon={Icons.Share}
                    // className="p-1"
                    label="Partager"
                    title="Partager ce document avec des personnes"
                />

                {!doc.is_read_only && isOpen && <DocumentShareModal {...props} {...{ setOpen, isOpen }} />}
            </div>
        </div>
    );
};

/**
 *
 * @param {*} props
 * @returns
 */

export default function DocumentUpdateView(props) {
    const { docSlug } = props.match.params;

    const [doc, setDoc] = useState(null);
    const [error, setError] = useState(null);

    const form = useForm({ title: "", reference: "", description: "" });
    const { setValues } = form;

    useEffect(() => {
        docServices
            .get(docSlug)
            .then((data) => {
                setDoc(data);
                setValues({
                    title: data.title || "",
                    reference: data.reference || "",
                    description: data.description || "",
                });
            })
            .catch((err) => {
                if (err.response) {
                    if (err.response.status === 404) {
                        setError("notfound");
                        return;
                    }
                }

                setError("error");
            });
    }, [docSlug, setValues]);

    if (error)
        return (
            <div>
                <h1>Ce document n'existe pas</h1>
            </div>
        );

    if (!doc || !doc.slug) return null;

    /**
     *
     * @param {Object} param0
     * @returns
     */
    const handleTextInputSave = ({ name, value }) => {
        if (!doc || doc.is_read_only) return;

        docServices
            .patch(docSlug, { [name]: value }, { [name]: doc[name] })
            .then((data) => {
                // console.log(data);
            })
            .catch((err) => {});
    };

    return (
        <Form context={form} className="document-update-form">
            <AdminView className="document-update-view miq-container-fluid">
                <ViewHeader {...props} {...{ doc, setDoc, handleTextInputSave }} />

                {/* FORM */}
                <DocumentUpdateForm {...props} {...{ docSlug, doc, setDoc, docSlug, form, handleTextInputSave }} />

                {/* FILES */}
                <div>
                    <div className="d-flex align-items-center justify-content-between">
                        <h4>Fichiers</h4>
                    </div>

                    <DocuFileGrid items={doc.files_data} />
                </div>
            </AdminView>
        </Form>
    );
}
