import PropTypes from "prop-types";
import { FileInput, IconButton, Icons } from "@miq/components";
import { docServices } from "./utils";
import { createRef } from "react";

const propTypes = {
    onSuccess: PropTypes.func,
};

export default function DocuFileUploadButton({ onSuccess, ...props }) {
    let uploadRef = createRef();

    function uploadFiles(evt) {
        // TODO: Ensure this filename has at most 100 characters

        Array.from(evt.target.files).forEach((file) => {
            docServices
                .postFile(file)
                .then((data) => {
                    if (onSuccess) onSuccess(data);
                })
                .catch((err) => {
                    console.log(err);
                });
        });
    }

    return (
        <>
            <IconButton
                Icon={Icons.PaperClip}
                label="Joindre"
                title="Joindre des fichiers à ce document"
                {...props}
                onClick={() => uploadRef.click()}
            />
            <FileInput onChange={uploadFiles} ref={(file) => (uploadRef = file)} />
        </>
    );
}

DocuFileUploadButton.propTypes = propTypes;
