import { useForm } from "@miq/form";
import { Img } from "@miq/components";
import SelectUserModal from "./SelectUserModal";
import AddUserModal from "./AddUserModal";

import "./document-share-modal.scss";

/**
 *
 * @param {*} props
 * @returns
 */

export default function DocumentShareModal(props) {
    const form = useForm({
        q: "",
        user: null,
        sendNotif: true,
        message: "",
    });

    const { isOpen } = props;

    if (!isOpen) return null;

    const closeModal = () => props.setOpen(false);

    if (form.values.user) return <AddUserModal {...props} {...{ form, closeModal }} />;

    return <SelectUserModal {...props} {...{ form, closeModal }} />;
}

export const UserCard = ({ user, form, ...props }) => {
    return (
        <div className="d-flex align-items-center">
            <div className="">
                <Img />
            </div>
            <div className="ms-1">
                <div className="">{user.name}</div>
                <div className="">@{user.username}</div>
            </div>
        </div>
    );
};
