import { Icons, Button, ImgSquare, IconNavLink } from "@miq/components";

import "./docufile-grid.scss";

const getIcon = (file) => {
    switch (file.ext) {
        case ".jpg":
        case ".jpeg":
        case ".png":
            return <ImgSquare {...file} />;

        default:
            const Icon = Icons.getFileIcon(file.ext);
            return <Icon className="icon mb-1" />;
    }
};

export default function DocuFileGrid({ items = [], ...props }) {
    return (
        <div className="docufile-grid">
            {items.map((file) => {
                return (
                    <Button title={file.name} className="docufile-btn" key={file.slug}>
                        {getIcon(file)}

                        <div className="text-center">
                            <span className="label d-block">{file.name_truncated}</span>
                            <span className="size d-block">{file.size}</span>
                        </div>
                    </Button>
                );
            })}
        </div>
    );
}
