import Form, { FormProvider } from "@miq/form";

import { isRequired, addForwardSlash } from "@miq/utils";
import { Icons, IconButton } from "@miq/components";
import { docServices } from "../utils";
import DocuFileUploadButton from "../DocuFileUploadButton";

export default function DocumentUpdateForm({ docSlug, ...props }) {
    const { form = isRequired("form"), doc, handleTextInputSave } = props;

    const addFile = (fileData) => {
        const { slug } = fileData;
        docServices
            .patchFile(docSlug, { action: "add", files: [slug] })
            .then((data) => {
                console.log(data);
                props.setDoc({ ...doc, ...data });
            })
            .catch((err) => {});
    };

    const handleArchive = (e) => {
        const url = `${addForwardSlash(props.match.url)}view/`;
        if (doc.status === "ARCHIVED") return props.history.push(url);

        docServices
            .archive(docSlug)
            .then(() => {
                props.history.push(url);
            })
            .catch((err) => {});
    };

    return (
        <FormProvider value={form}>
            <div className="d-flex align-items-center justify-content-between my-2">
                <div className="d-flex">
                    <IconButton
                        Icon={Icons.Trash}
                        label="Supprimer"
                        title="Supprimer ce document"
                        onClick={() =>
                            docServices
                                .delete(docSlug)
                                .then((res) => {
                                    props.history.push("/grio/documents/");
                                })
                                .catch((err) => {})
                        }
                        className="me-1"
                        disabled={doc.is_read_only}
                    />

                    <IconButton
                        Icon={Icons.Archive}
                        onClick={handleArchive}
                        label="Archiver"
                        title="Archiver ce document. Un document archivé ne peut plus être modifié"
                        disabled={doc.is_read_only}
                        // className="p-1"
                    />
                </div>
                <div className="actions">
                    <DocuFileUploadButton onSuccess={addFile} disabled={doc.is_read_only} className="p-1" />
                </div>
            </div>

            <div className="d-grid grid-lg-12 mb-2">
                <Form.Label value="Reference" className="span-12 mb-1" />
                <Form.TextInput
                    name="reference"
                    onSave={handleTextInputSave}
                    autoComplete="off"
                    className="span-12 span-md-4"
                    placeholder="Ajouter une reference ..."
                    disabled={doc.is_read_only}
                />
            </div>

            <div className="d-grid grid-1 grid-xl-12 mb-2">
                <Form.Label value="Description" className="span-12 mb-1" />

                <div className="span-lg-8">
                    <Form.TextAreaX
                        name="description"
                        onSave={handleTextInputSave}
                        placeholder="Ajouter une description ..."
                        disabled={doc.is_read_only}
                    />
                </div>
            </div>
        </FormProvider>
    );
}
