import { debounce, throttle } from "lodash";
import { useCallback, useEffect, useRef, useState } from "react";

import Form from "@miq/form";
import { Portal, Button, IconButton, Icons, Collapse } from "@miq/components";
import { docServices } from "../../utils";
import { UserCard } from ".";

/**
 *
 * @param {*} props
 * @returns
 */

export const Footer = (props) => {
    const { form } = props;
    return (
        <div className="dms-share-footer">
            <div className="">
                <Button primary onClick={props.closeModal}>
                    Terminé
                </Button>
            </div>
        </div>
    );
};

export default function SelectUserModal({ form, doc, ...props }) {
    const [q, setQ] = useState("");
    const [users, setUsers] = useState([]);

    const getStaffUsers = (q) =>
        docServices.getStaffList(q).then(({ results }) => {
            // if (results)
            setUsers(results);
        });
    const debounceGetStaffUsers = useCallback(debounce(getStaffUsers, 400), []);

    useEffect(() => {
        if (!q || q.length < 3) return;

        debounceGetStaffUsers(q);
    }, [q, debounceGetStaffUsers]);

    if (!form || !doc) return null;

    const setUser = (user) => {
        if (doc.users.includes(user.slug)) return;
        form.setValue("user", user);
    };

    return (
        <Portal>
            <Form context={form} id="DocumentShareModal" className="dms-share">
                <Portal.Modal
                    header={
                        <div className="dms-share-header">
                            <div className="title">
                                <div className="btn btn-icon select">
                                    <Icons.PersonPlus className="icon" />
                                </div>
                                <h2>Partager avec des personnes</h2>
                            </div>

                            <div className="actions">
                                <IconButton Icon={Icons.X} onClick={props.closeModal} />
                            </div>
                        </div>
                    }
                    // FOOTER
                    footer={<Footer {...props} {...{ form }} />}
                >
                    <div className="dms-share-select">
                        {/* <SelectUserTab {...props} {...{ form }} /> */}

                        <Collapse
                            floating
                            header={
                                <input
                                    required
                                    name="q"
                                    onChange={(e) => setQ(e.target.value)}
                                    placeholder="Ajouter des personnes"
                                    className="miq-input mb-1"
                                />
                            }
                            isOpen={q.length > 2 && users.length > 0}
                        >
                            {users.map((user, i) => (
                                <div
                                    className="dms-user-item"
                                    onClick={() => setUser(user)}
                                    key={`${user.slug}-${i}`}
                                    role="button"
                                >
                                    <UserCard {...{ user, form }} />
                                </div>
                            ))}
                        </Collapse>

                        <div className="dms-share-users my-2">
                            {doc.users_data.map((user) => {
                                return (
                                    <div
                                        className="my-1 d-flex align-items-center justify-content-between"
                                        key={user.slug}
                                    >
                                        <UserCard {...{ user, form }} />

                                        <div className="d-flex align-items-center">
                                            <DocuSharedPermissionSelect
                                                {...{ user, doc, form, setDoc: props.setDoc }}
                                                className="me-1"
                                            />
                                            <IconButton
                                                Icon={Icons.X}
                                                onClick={() => {
                                                    docServices
                                                        .patchUser(doc.slug, { action: "remove", users: [user.slug] })
                                                        .then((data) => {
                                                            props.setDoc({ ...doc, ...data });
                                                        })
                                                        .catch((err) => {
                                                            console.log(err);
                                                        });
                                                }}
                                                title="Supprimer cet utilisateur"
                                            />
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                </Portal.Modal>
            </Form>
        </Portal>
    );
}

const DocuSharedPermissionSelect = ({ user, doc, ...props }) => {
    const [value, setValue] = useState(user.permission || "VIEW");

    const permissions = Object.values(doc.permissions);

    return (
        <select
            value={value}
            className="miq-select me-1"
            onChange={(e) => {
                const { value } = e.target;
                setValue(value);
                docServices
                    .patchUserPermission(doc.slug, user.slug, value)
                    .then((data) => props.setDoc({ ...doc, ...data }));
            }}
        >
            {permissions.map((perm) => (
                <option key={perm.name} value={perm.value}>
                    {perm.label}
                </option>
            ))}
        </select>
    );
};

/**
 *
 * @param {*} param0
 * @returns
 */

export function SelectUserTab({ doc, form = {}, ...props }) {
    const q = form.values.q;
    const [users, setUsers] = useState([]);
    const [width, setWidth] = useState(null);

    const getStaffUsers = (q) =>
        docServices.getStaffList(q).then(({ results }) => {
            if (results) setUsers(results);
            console.log("her");
        });
    const debounceGetStaffUsers = useCallback(debounce(getStaffUsers, 400), []);

    useEffect(() => {
        if (!q || q.length < 3) return;
        debounceGetStaffUsers(q);
    }, [q, debounceGetStaffUsers]);

    const ref = useRef(null);

    useEffect(() => {
        const el = ref.current;
        if (!el || typeof window !== "object") return;

        const resize = throttle(
            () => {
                const rect = el.getBoundingClientRect();
                setWidth(rect.width);
            },
            400,
            { trailing: true }
        );

        window.addEventListener("resize", resize);
        resize();

        return () => window.removeEventListener("resize", resize);
    }, []);

    const setUser = (user) => {
        if (!form || doc.users.includes(user.slug)) return;
        form.setValue("user", user);
    };

    return (
        <div className="dms-dropdown">
            <div className="dms-dropdown-header">
                <Form.TextInput required name="q" placeholder="Ajouter des personnes" className="mb-1" ref={ref} />
            </div>

            {users && users.length !== 0 && (
                <div className="dms-dropdown-body" style={{ width }}>
                    <ul className="inner">
                        {users.map((user, i) => {
                            return (
                                <li
                                    className="dms-dropdown-item"
                                    onClick={() => setUser(user)}
                                    key={`${user.slug}-${i}`}
                                    role="button"
                                >
                                    <UserCard {...{ user, form }} />
                                </li>
                            );
                        })}
                    </ul>
                </div>
            )}
        </div>
    );
}
