import Form from "@miq/form";
import { Portal, Icons, Button, IconButton } from "@miq/components";
import { UserCard } from ".";
import { docServices } from "../../utils";

/**
 *
 * @param {*} props
 * @returns
 */

export const Footer = (props) => {
    const { addUser } = props;

    return (
        <div className="dms-share-footer">
            <div className="">
                <Button onClick={addUser}>Envoyer</Button>
            </div>
        </div>
    );
};

export default function AddUserModal({ form, doc, ...props }) {
    const user = form.values.user;

    if (!form) return null;

    const addUser = () => {
        const { slug } = form.values.user;
        if (slug === doc.uploaded_by.slug) {
            return;
        }

        docServices
            .patchUser(doc.slug, { action: "add", users: [slug] })
            .then((data) => {
                props.setDoc({ ...doc, ...data });
                form.setValue("user", null);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    return (
        <Portal>
            <Form context={form} id="DocumentShareModal" className="dms-share">
                <Portal.Modal
                    /**
                     * HEADER
                     */
                    header={
                        <div className="dms-share-header">
                            <div className="title">
                                <div>
                                    <IconButton
                                        Icon={Icons.ArrowLeft}
                                        onClick={() => form.setValue("user", "")}
                                        title="Retourner derriere"
                                        className="add"
                                    />
                                </div>
                                <h2>Partager avec des personnes</h2>
                            </div>

                            <div className="actions"></div>
                        </div>
                    }
                    /**
                     * FOOTER
                     */
                    footer={<Footer {...props} {...{ addUser }} />}
                >
                    <div className="dms-share-add">
                        <div className="">
                            <UserCard {...{ user }} />
                        </div>

                        <div className="add-form mt-2">
                            <Form.CheckboxInput
                                name="sendNotif"
                                checked={form.values.sendNotif}
                                label="Envoyer une notification"
                            />

                            <Form.TextArea
                                name="message"
                                placeholder="Message"
                                maxLength={200}
                                rows={4}
                                className="my-1"
                            />
                        </div>
                    </div>
                </Portal.Modal>
            </Form>
        </Portal>
    );
}
